.intro{
    position: absolute;
    left: 10%;
    top: 25%;
    transform: translate(-10%, 0);
    animation: fade-in 2.4s ease-out 0s;
    
    padding: 10px;
    margin: 10px;

    border-image: linear-gradient(to left ,rgba(0, 255, 255, 0.0), rgb(136, 0, 136)) 20;
    border-width: 5px;
    border-style: solid;

    *{
        position: relative;
        border: 0;
    }
    
}

.statement{
    background-image: linear-gradient(to right, rgba(128, 128, 128, 0.814), rgba(33, 33, 33, 0.151)); 
    padding: 10px;
    margin: 10px;

    h1, p{
        color: rgb(255, 255, 255);
        font-weight: bold;
        font-size: min(3vw, 3vh);
        font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    }
}
.photo{
    transform: translate(10px, 0);
    display: flex;
    justify-content: center;
    img {
        $imgsize: min(30vw, 30vh);
        object-fit: cover;
        width: $imgsize;
        height: $imgsize;
    }
}


@keyframes fade-in {
    0% { opacity: 0; }
    100% { opacity: 1; }
}